@import "../App.css";

#SlideShow{
/*	text-align: center;
	display: inline-block;*/
}

#SliderShowCodeInfo{
	margin-bottom: 50px;
}

.invisibleVertSep{
	width: 100%;
	height: 50px;
}

#SlideShowInfo{
}

#CredentialsDiv{
	margin-top: -20px;
	z-index: 10;
}
#ImageCredentialsHeading{
	color: var(--main-bg-color);
}
#ImageCredentials{
	display: inline-block;
	margin-bottom: 20px;
	margin-top: 5px;
}
.ImageCredentialTag{
	display: inline-block;
	background: #00000011;
	color: #f4b342;
	padding: 3px 5px;
	padding-bottom: 5px;
	border-radius: 5px;
	font-weight: bold;
	cursor: pointer;
}
.ImageCredentialInset{
	display: inline-block;
	width: 10px;
	height: 100%;
}

#canvasDiv{
	display: block;
	margin: auto;
	position: relative;
	left: 0;
	width: 80vw;
	height: 60vw;
	max-width: 1500px;
	max-height: 800px;
	z-index: 0;
}
#loadingDivSS{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
}
#WaitingBarDivSS{
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	max-width: 600px;
	height: 100%;
	max-height: 400px;
	z-index: 1;
}
#loadingDivMsgSS{
	position: relative;
	top: 10%;
	width: 100%;
	font-size: 15vw;
	font-weight: bold;
	color: lime;
	animation-name: loadingDivMsgAnim;
	animation-duration: 4s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
}
@keyframes loadingDivMsgAnimSS {
  0%   {color: #ff4400; font-size: 7vw; transform: rotate(0deg);}
  25%  {transform: rotate(-10deg);}
  50%  {color: #00ff00; font-size: 4vw; transform: rotate(0deg);}
  75%  {transform: rotate( 10deg);}
  100% {color: #ff4400; font-size: 7vw; transform: rotate(0deg);}
}
#slideShowCanvas{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.AnimationKeyHeading{
	display: inline-block;
	font-weight: bold;
	color: #008800;
/*	background-color: #00ff0011;*/
	margin-bottom: 5px;
	margin-top: 10px;
	padding: 10px;
	border: 1px solid #008800;
/*	border-radius: 5px;*/
}
.AnimationKeyButtons{
	padding-top: 10px;
}
.AnimKeyBtn{
	display: inline-block;
/*	background: #00440000;*/
	font-weight: bold;
	color: #004400;
	padding: 5px;
	margin: 0 2px;
/*	border-radius: 5px;*/
	border: 1px solid #008800;
	
	width: 100px;
}
.AnimKeyBtn:hover{
	background: #880000;
	color: white;
	cursor: pointer;
	transition-duration: 1s;
}
.AnimKeyBtn.AnimKeyBtnSelected{
	background: #000088ff;
	color: #00aa00;
}
