.MainDivWWB{
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 100%;
}
.CanvasWWB{
  grid-column: 1;
  grid-row: 1;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.LabelWWB{
  grid-column: 1;
  grid-row: 1;
  color: #008800;
  font-size: 10px;
  font-weight: bold;
  text-align: center;
  z-index: 1;
  -webkit-animation-name: fadingWWB;
          animation-name: fadingWWB;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes fadingWWB {
  0%    {opacity: 1; -webkit-transform: scale(1); transform: scale(1);}
  50%   {opacity: 0.0; -webkit-transform: scale(0.75); transform: scale(0.75);}
  100%  {opacity: 1; -webkit-transform: scale(1); transform: scale(1);}
}

@keyframes fadingWWB {
  0%    {opacity: 1; -webkit-transform: scale(1); transform: scale(1);}
  50%   {opacity: 0.0; -webkit-transform: scale(0.75); transform: scale(0.75);}
  100%  {opacity: 1; -webkit-transform: scale(1); transform: scale(1);}
}


