.CheckBoxDivRSC{
	display: flex;
	font-size: 20px;
	margin: auto;
	width: 100%;
	justify-content: center;
	margin-bottom: 20px;
}
.CheckBoxLabelRSC{
	font-size: 20px;
	margin-right: 20px;
}
.CheckBoxRSC{
	margin-top: 4px;
}

/*---------------------------*/

#CodeDivRSC{
	display: grid;
	grid-template-rows: 50px 25px auto;
	grid-template-areas: "heading" "codeFiles" "code";

	width: 95vh;
	max-width: 1200px;
	height: 60vh;
	max-height: 800px;
	margin: auto;
	border: 1px solid black;
}
#CodeHeadingRSC{
	font-weight: bold;
	font-size: 20px;
	margin: auto;
	width: 100%;
	height: 100%;
	text-align: center;
	color: black;
}
#CodeFilesRSC{
	display: flex;
	justify-content: center;
	border: 1px solid black;
}
.CodeFileRSC{
	margin: auto 5px;
	background-color: lightgreen;
	border: none;
	border-radius: 2px;
	font-weight: bold;
	font-size: 15px;

	transition-duration: 0.4s;
}
.CodeFileRSC:hover{
	background-color: var(--Nili-color-light);
}
#CodeRSC{
	width: 100%;
	height: 100%;
	overflow: auto;
}
