.ImagePreviewContainer{
	position: relative;

	width: 100%;
	height: 100%;
}

.ImagePreviewIndicatedImage{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;

	overflow: hidden;

	cursor: pointer;
}
.ImagePreviewImage{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;

	cursor: pointer;

	transition-duration: 1s;
}
.ImagePreviewIndicatedImage:hover .ImagePreviewImage{
	transform: scale(1.1);
}
.PreviewImageIndicatorDiv:hover .ImagePreviewImage{
	transform: scale(1.1);
}
.FocusedScaledMIP{
	transform: scale(1.1);
}


.ImagePreviewIndicatedImage:hover .PreviewImageIndicatorDiv{
	left: 0%;
}

.PreviewImageIndicatorDiv{
	position: absolute;
	bottom: 0;
	left: -100%;
	width: 100%;
	height: 40%;

	background-color: #005500dd;
	text-align: left;
	transition-duration: 0.5s;

}
.PreviewImageIndicatorHeadingDiv{
	background-color: #008800;
	margin-bottom: 5px;
	padding-top: 2px;
	padding-bottom: 2px;
}
.PreviewImageIndicatorHeading{
	margin: 0.4em;
	width: 80%;
	font-weight: bold;
	color: white;
}
.PreviewImageIndicatorDescriptionDiv{

}
.PreviewImageIndicatorDescription{
	width: 80%;
	margin-left: 0.4em;
	font-weight: normal;
	color: white;
}

.FocusedLeftMIP{
	left: 0%;
}
