

.SpinningWheelRSC{
	display: grid;
	grid-template-columns: 100%;

}
#SpinningWheelHelperGrid{
	display: grid;
	grid-template-columns: minmax(15rem, 300px);
	grid-auto-rows: minmax(15rem, 300px);
	justify-content: center;
	padding: 0 5px;
}
.SpinningWheelDivRSC{
	border-radius: 20px;
	width: 100%;
	height: 100%;
	background-color: #888;
}
.SpinningWheelResultsRSC{
	font-size: 40px;
	
	margin: auto;
	margin-top: 10px;
	margin-bottom: 30px;
}

